
@import "../module_scss/font_customize";

$font_color: #333;
$btnSide_color: rgba(255, 255, 255, 0.219);
$logo_color: #ff7b1a;
$logo_color_hover: #ff791ab7;

$main_item_imgW: 230px;
$main_item_imgH: 153.4px;


@keyframes nav {
    from {
        top: -43px;
    }
    to {
        top: -6.5px;
    }
}

*{
    font-family: "Microsoft YaHei";
    scroll-behavior: smooth;
}

//Home
.Main_container{
    width: 100%;
    height: 808px;
    max-width: 1980px;
    margin-left: auto;
    margin-right: auto;
}

//Header
.header{
    z-index: 1;
    display: block;
    position: fixed;
    top: 0;
    width: 100%;
    height: 105px;
    background-color: rgba(255, 255, 255, 0.315);
    

    .logo{
        display: inline-block;
        background-image: url(../images/TTTTaiwan-V2.png);
        background-size: 100%;
        background-repeat: no-repeat;
        width: 95px;
        height: 95px;
        position: absolute;
        top: 5px;
        left: 26%;


    }
    .nav_container{
        position: absolute;
        top: 32px;
        right: 3%;
        width: 40%;
        height: 300px;
        

        .nav_item{
            
            display: inline-block;
            text-align: center;
            margin-right: 5%;
            margin-left: 5%;
            
            
            

            .a:link,:visited{
                @include font(3);

                text-decoration-line: none;
                color: $font_color;
            }
            .a{
                display: block;
                width: 97px;
                height: 80px;
            }
            .a:hover{
                color: #000;
            }

        }
        .hover_nav{
                animation-name: nav;
            animation-duration: 0.28s;
            animation-iteration-count: 1;
                

                width: 103%;
                height: 140px;
                background-color: rgba(255, 255, 255, 0.541);
                position: relative;
                top: -7px;
                text-align: center;
                transform: translate(-14%, 0);

                .hover_item{
                    @include font(1);

                    color: #623A28;
                    text-align: center;
                    font-weight: 850;
                    line-height: 50px;
                    cursor: pointer;
                    
                    display: inline-block;
                    margin-right: 0.45vw;
                    margin-left: 0.45vw;
                    margin-top: 12px;
                    
                    width: 16%;
                    height: 120px;
                    
                    background-size: 45%;
                    background-position: 50% 60% ;
                    background-repeat: no-repeat;
                    
                }
                .hover_item:hover{
                    background-color: rgba(255, 255, 255, 0.200);
                    border-radius: 50%;
                    
                }
                .line{
                    display: inline-block;
                    width: 2px;
                    height: 80px;
                    background-color: #ff791a8c;
                    margin-left: 1px;
                    color: #00000000;
                }
            }
        
    }
}

//Banner
.banner_container{
    width: 100%;
    position: relative;
    z-index: 0;
    overflow: hidden;

    .banner_repeater{
        display: flex;
        width: 100%;
        margin-left: 0%;
        transition: all 0.7s;
        
        .img_container{
            
            width: 100%;
            flex-shrink: 0;
            
            img{
                width: 100%;
                height: 100%;
                
            }
        }
    }
    .handle_carousel{
        display: flex;
        position: absolute;
        bottom: 2%;
        right: 50%;
        transform: translate(50%, 0);

        .handle_btn{
            width: 12px;
            height: 12px;
            border: solid 2px rgba(255, 255, 255, 0.219);
            border-radius: 50%;
            margin-right: 10px;
            margin-left: 10px;
            background-color: rgba(255, 255, 255, 0.055);
            cursor: pointer;
            transition: all 0.7s;

        }
    }
    .input_container{
        display: flex;
        justify-content: center;
        align-items: center;
        
        position: absolute;
        top: 444px;
        right: 50%;
        transform: translate(50%,0);
        background-color: white;
        border-radius: 7px;
        width: 28vw;
        height: 45px;
        
        
        i{
            @include font(2);

        }
        input{
            @include font(1);

            width: 18vw;
            height: 38px;
            margin-left: 10px;
            color: $font_color;
            background-color: white;
            border: solid 1px white;
        }
        input:focus{
            border: solid 2px $logo_color;
            border-radius: 5px;
            outline: solid white 1px
        }
        .GO_btn{
            @include font(1,29px);

            font-weight: 600;
            text-align: center;

            cursor: pointer;
            
            width: 94px;
            height: 30px;
            background-color: $logo_color;
            border-radius: 20px;
            margin-left: 20px;
        }
        .GO_btn:hover{
            background-color: $logo_color_hover;
        }
    }
}

//ActivityMain
.first{
    position: relative;
    width: 100%;
    height: 500px;

    .main_title{
        width: 54%;
        margin-top: 100px;
        margin-bottom: 30px;
        margin-right: auto;
        margin-left: auto;
        
        @include font(4);
        
        font-weight: 550;
    }
    .main_title::before{
        content: ".";
        color: #00000000;
        
        background-color: $logo_color;
    }
    .main_items_container{
        margin-left: auto;
        margin-right: auto;
        width: 57%;

        display: flex;
        justify-content: start;
        flex-wrap: wrap;
        
    }
    .btn_container{
        width: 50%;
        height: 30px;
        margin: 20px auto 80px auto;

        display: flex;
        justify-content: center;

        .left_btn{
            @extend .btn;

        }
        .right_btn{
            @extend .left_btn;
        }
    }
}

//MainItems
.item_a{
    flex-basis: 240px;
    height: 270px;
    margin-left: 14px;
    margin-right: 14px;
    margin-bottom: 45px;
    text-align: center;

    .item_container{
        
        i{
            display: inline;
        }
        img{
            width: $main_item_imgW;
            height: $main_item_imgH;
        }
        .title_container{
            
            width: 200px;
            height: 84px;
            margin-left: auto;
            margin-right: auto;

            overflow: hidden;
            

            .item_title{
                
                @include font(2);
                font-weight: 600;
                
                
            }
        }
        .caption_container{
            margin-top: 10px;

            i{
                color: $logo_color;
            }
            .item_caption{
                display: inline;
                color: $font_color;
                font-weight: 600;
            }
        }
    }
}
.item_a:visited, :link{
    color: $font_color;
    text-decoration: none;
}

//MainPageBtn    ****** 收進SCSS_ALL ******
.btn{
    font-weight: 500;

    text-align: center;
    width: 25px;
    height: 25px;

    border-radius: 50%;
    margin-left: 3px;
    margin-right: 3px;
    cursor: pointer;

    
    
}
.btn:hover{
    color: white;
    background-color: $logo_color;
}

//ScrollTop      ****** 收進SCSS_ALL ******
.to_top_btn_container{
    position: fixed;
    right: 15px;
    bottom: 10px;
    width: 45px;
    height: 45px;
    
    border-radius: 10px;
    background-color: $logo_color;

    cursor: pointer;

    .to_top_btn{
        width: 19px;
        height: 34px;
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);

        font-size: 25px;
        color: white;
        background-color: $logo_color;
    }
}
.to_top_btn_container:hover{
    opacity: 0.85;
}
